.card {
  width: 200px;
  height: 298px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  border: 2px solid black;
  border-radius: 5px;
  transition: all 0.25s;
  background-color: $color-bg-card;
  &:hover {
    transform: scale(1.05);
    box-shadow: -10px 0px 13px -7px rgb(122, 189, 68),
      10px 0px 13px -7px rgb(122, 189, 68),
      5px 5px 15px 5px rgba(179, 255, 37, 0.719);
  }
  &__img {
    display: block;
    margin: auto;
    width: 100%;
    height: auto;
  }
  &__title {
    margin-top: 0.8rem;
    text-transform: capitalize;
    font-family: $font-title;
    color: black;
    font-size: 1.1rem;
  }
  &__specie {
    margin-top: 0.6rem;
    font-family: $font-main;
    color: black;
    font-weight: 300;
  }
}

.fa-specie {
  margin-left: 0.5rem;
  font-size: 0.8rem;
}
